#skills{
  overflow: hidden;
  width: 100vw;
  max-width: 65rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 0 auto;
}

.skillsTitle{
  font-size: 2.4rem;
  font-weight: 600;
  padding-top: 3rem;
  margin-bottom: 1.5rem;
}

.skillDescription{
  font-weight: 20;
  font-size: 1rem;
  max-width: 50rem;
  padding: 0 2rem;
}

.skillBars{
  margin:1.5rem;
  width: 100vw;
  max-width: 80%;
  text-align: left;
}

.skillBar{
  display:flex;
  flex-direction: column;
  margin:1rem;
  padding: 1.5rem 2rem;
  border-radius: 0.5rem;
  background-color: rgb(50,50,50);
}

.description{
  font-size: 0.9rem;
  
}

.descriptionContainer{
  padding: 1rem;
}
